.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 12px;
  font-family: "Inter";
  line-height: 28px;
  font-style: normal;
}
.btn {
  padding: 24px 32px;
  width: 1152px;
  height: 76px;
  background: #eaecf0;
  border: 1px solid #eaecf0;
  border-radius: 8px;
  text-align: center;
}
.btn h5 {
  font-size: 18px;
  font-weight: 500;
  color: #101828;
}
